import { useState } from "react";
import { ReactComponent as BackgroundImage } from "../../../assets/auth/girlSittingWifhLaptop.svg";
import { ReactComponent as Logo } from "../../../assets/auth/justLikeLogo.svg";
import RoundedButton from "../../Common/RoundedButton/RoundedButton";
import classes from "./SignUp2.module.css";
import { logIn } from "../../../services/AuthService";
import ComboBox from "../../Common/ComboBox/ComboBox";

function SignUp2() {
  const [data, setData] = useState({
    firstName: "",
    lastName: "",
    phone: "",
    linkedin: "",
    email: "",
    password: "",
  });

  const handleChange = (e: any) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e: any) => {
    e.preventDefault();

    logIn(data.email, data.password);
  };

  const userTypes = ["'\\ ", "", " "];
  const interests = [" ", "", ""];
  const specialties = [
    "",
    " ",
    " ",
    " ",
    "",
  ];

  return (
    <div className={classes.main}>
      <div className={classes.form}>
        <form onSubmit={handleSubmit}>
          <div className={classes.formContent}>
            <Logo width="125" height="125" />
            <h1>  </h1>

            <ComboBox placeHolder=" *" items={userTypes} />
            <ComboBox placeHolder=" *" items={interests} />
            <ComboBox placeHolder=" " items={specialties} />

            <RoundedButton style="width:50px"></RoundedButton>
            <div className={classes.signupContainer}>
              <span> ?</span>
              <a href="#"></a>
            </div>
          </div>
        </form>
      </div>

      <div className={classes.backgoundImageContainer}>
        <BackgroundImage width="722" height="674" />
      </div>
    </div>
  );
}

export default SignUp2;
